                        </li>
                        <li>1个活动<van-icon name="arrow" /></li>
                    </ul>
                </section>
            </div> -->
            <div class="head">
                <ul>
                    <li>
                        <p>商品</p>
                        <p></p>
                    </li>
                    <li>
                        <p>评价</p>
                        <p></p>
                    </li>
                </ul>
            </div>
            <div class="item">
                <div class="dian" >
                    <van-sidebar v-model="activeKey">
                        <van-sidebar-item :title="item.name" v-for="(item,index) of info" :key="index" @click="tab(index)" />
                    </van-sidebar>
                </div>
                <div class="item-di" >
                    <div class="add" v-for="(item,index) in info" :key="index" v-show="item.show">
                        <ul>
                            <li><h3>{{item.name}}</h3></li>
                            <li>{{item.description}}</li>
                            <li><van-icon name="more-o" /></li>
                        </ul>
                        <div class="cont" v-for="(i,key) in item.foods" :key="key">
                            <section><img :src="'//elm.cangdu.org/img/'+i.image_path" alt=""></section>
                            <section>
                                <div class="name"><b>{{i.name}}</b></div>
                                <div>{{i.description}}</div>
                                <div>月售{{i.month_sales}}份 好评率{{i.satisfy_rate}}%</div>
                                <div class="mine" v-for="(arr,one) in i.specfoods.slice(arr,1)" :key="one">
                                    <div>￥<b>{{ arr.price }}</b></div>
                                    <!-- <div v-if="i.is_featured>0" @click="jian(i)"><van-icon name="close" /></div>
                                    <div v-if="i.is_featured>0">{{i.is_featured}}</div> -->
                                    <div @click="add($event,i)"><van-icon name="add-o" /></div>
                                </div>
                            </section>  
                        </div>
                    </div>
                </div>
                <div class="gwc">
                    <van-action-sheet v-model="show" class="sheet">
                        <div class="del">
                            <ul>
                                <li>购物车</li>
                                <li @click="del"><van-icon name="delete"/>清空</li>
                            </ul>
                        </div>
                        <div class="item-gwc" v-for="(item,index) of gwc" :key="index">
                            <div><h3>{{item.name}}</h3></div>
                            <div v-for="(v,one) of item.specfoods.slice(arr,1)" :key="one"><h3>￥{{v.price}}</h3></div>
                            <div>
                                <div><van-icon name="close" @click="jian(item.item_id)"/></div>
                                <div>{{item.num}}</div>
                                <div><van-icon name="add-o" @click="jia(item.item_id)"/></div>
                            </div>
                        </div>
                    </van-action-sheet>
                </div>
            </div>
        </div>
        <div class="yuan"></div>
        <div class="ding">
            <div class="mony">
                <van-button type="primary" @click="showPopup" class="primary">
                    <div><b>￥{{price}}</b></div>
                    <div>配送费￥5</div>
                </van-button>
            </div>
