</template>

<script>
import  '../../config/theball/theBall.css'
import tball from '../../config/theball/theBall.js'
    export default {
        data() {
            return {
                activeKey:0,
                info:[],
                name_id:"",
                homeImg:"",
                homeTitle:"",
                homeMone:"",
                show:false,
                arr:[],
                one:false
            }
        },
        mounted() {
            this.name_id=this.$route.query.id
            this.http.get("http://elm.cangdu.org/shopping/v2/menu?restaurant_id="+this.name_id+"").then((res)=>{
                this.info=res.data
            })
        },
        methods: {
            tab:function(index){
                for(let i=0;i<this.info.length;i++){
                    this.info[i].show=false;
                }
                this.info[index].show=true
            },
            showPopup() {
                this.show = true;
            },
            add(event,i){
               tball(event)
               this.$store.commit('addList',i)
            },
            jia(id){
                this.$store.commit('jia',id)
            },
            jian(id){
                this.$store.commit('jian',id)
            },
            del(id){
                this.$store.commit('delete')
            }
        },
        computed: {
            gwc(){
                return this.$store.state.gwc
            },
            price(){
                return this.$store.getters.priv
            }
        },
    }
</script>

