}
.gwc{
    // width: 100%;
    z-index: 1;
}
.sheet{
    width: 100%;
    height: 8rem;
    // overflow: auto;
    position: fixed;
    top:6rem;
}
.yuan{
    width: 1.3rem;
    height: 1.3rem;
    z-index: 10;
    border-radius: 50%;
    border: 0.05rem solid rgb(192, 179, 179);
